.input {
  display: flex;
  flex-direction: column;
  margin: 1em 0;
  width: 100%;
}

.input > input {
  background-color: #f7f3f5;
  border-radius: 15px;
  border-width: 0;
  height: 2em;
  margin-top: 0.5em;
  padding: 0.3em 1em;
  border: 1px solid #8da9c4;
}

.input__error > input {
  border-color: red;
}

.input > span {
  margin-left: 0.8em;
  font-size: 0.8em;
}

.input > span:nth-of-type(2) {
  height: 1.5em;
}

.input__error > span:nth-of-type(2) {
  margin-top: 0.2em;
  font-size: 0.7em;
  color: red;
}
